/*geral*/
*{margin: 0; padding: 0; -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; font-size: 18px; font-family: Arial, Helvetica, sans-serif;}

a{text-decoration: none; font-weight: bold;}

#foto1{max-width: 180px;}

.foto{float: left; width: 30%; padding: 5px 0 15px 1%;}

.nav{background: #000000}
    .nav a{text-align: center; padding: 10px 0; color: #ffffff;}
    .nav a:hover{background: gray;}

h1{font-size: 28px;padding: 100px 0 15px 5%;}
h2{font-size: 16px;padding: 2px 0 2px 15px;}
h3{font-size: 14px;padding: 10px 0 0 15px;}
#titulo{background-color: #000000; color: #ffffff;}
li{font-size: 16px;padding: 10px 0 0 15px;}
h4{font-weight: normal; font-size: 14px;}

.formulario{display: flex; width: 35%; margin: 20px 25%; flex-direction: column;}
    .formulario input{margin: 6px; height: 25px;}
    .formulario textarea{width: 100%; height: 100px; padding: 5px;}

.footer{background-color: #000000; color: #ffffff; font-size: 11px; text-align: center; padding: 8px 3%;}

.redessociais td{display: table-cell; padding: 3px; vertical-align: middle; text-align: left;}
.redessociais img{width: 20px; height: 20px;vertical-align: middle;}
.redessociais a{font-size: 11px;}
.redessociais h3{font-size: 12px;}

/*tela pequena (smartphones)*/    
@media screen and (max-width: 480px) {
    .nav{display:flex; flex-direction: column;}
        .nav a{width: 100%;}
}

/*tela média (smartphones e tablets)*/
@media screen and (min-width: 481px) and (max-width: 768px) {
    .nav{display:flex; flex-direction: column;}
        .nav a{width: 100%;}
}

/*tela média (notebooks)*/
@media screen and (min-width: 769px) and (max-width: 1199px) {
    .nav{display:flex; position: fixed; width: 100%;}
        .nav a{width: 25%;}
}

/*tela grande (notebooks)*/
@media screen and (min-width: 1200px) {
    .nav{display:flex; position: fixed; width: 100%;}
        .nav a{width: 25%;}
}
